<template>
  <div class="add-risk-level">
    <Form
      ref="formValidate"
      :model="formValidate"
      :rules="ruleValidate"
      :label-width="100"
      class="form-content"
    >
      <FormItem label="规则名称" prop="bus_code">
        <Input v-model="formValidate.bus_code" placeholder="请输入规则名称"></Input>
      </FormItem>
      <FormItem label="规则模型" prop="bus_type">
        <Select v-model="formValidate.bus_type" placeholder="请输入规则模型">
          <Option value="beijing">New York</Option>
          <Option value="shanghai">London</Option>
          <Option value="shenzhen">Sydney</Option>
        </Select>
      </FormItem>
      <FormItem label="需要调整等级" prop="bus_type">
        <Select v-model="formValidate.bus_type" placeholder="请输入需要调整等级">
          <Option value="beijing">New York</Option>
          <Option value="shanghai">London</Option>
          <Option value="shenzhen">Sydney</Option>
        </Select>
      </FormItem>
      <FormItem label="目标等级" prop="bus_type">
        <Select v-model="formValidate.bus_type" placeholder="请输入目标等级">
          <Option value="beijing">New York</Option>
          <Option value="shanghai">London</Option>
          <Option value="shenzhen">Sydney</Option>
        </Select>
      </FormItem>
      <FormItem label="优先级" prop="bus_name">
        <Input v-model="formValidate.bus_name" placeholder="请输入优先级"></Input>
      </FormItem>
      <FormItem label="备注" prop="bus_name">
        <Input
          v-model="formValidate.bus_name"
          type="textarea"
          :autosize="{minRows: 2,maxRows: 5}"
          placeholder="请输入业务描述"
        ></Input>
      </FormItem>
      <FormItem >
        <div class="button-content">
          <Button type="primary" @click="handleSubmit('formValidate')">保存</Button>
          <Button @click="handleCancel">取消</Button>
        </div>
      </FormItem>
    </Form>
  </div>
</template>
<script>
export default {
  props: ["dataObj"],
  data() {
    return {
      formValidate: {
        bus_code: "",
        bus_type: "",
        bus_name: "",
        department: "",
        interest: [],
        create_date: "",
        bus_describe: ""
      },
      ruleValidate: {
        busis: [
          {
            required: true,
            message: "业务类型不能为空！",
            trigger: "change"
          }
        ],
        name: [
          {
            required: true,
            message: "业务代码不能为空！",
            trigger: "blur"
          }
        ],
        personer: [
          {
            required: true,
            message: "业务名称不能为空！",
            trigger: "blur"
          }
        ],
        department: [
          {
            required: true,
            message: "所属业务部门不能为空！",
            trigger: "change"
          }
        ],
        create_date: [
          {
            required: true,
            type: "date",
            message: "建立日期不能为空！",
            trigger: "change"
          }
        ],
        bus_describe: [
          {
            required: true,
            message: "业务描述不能为空！",
            trigger: "blur"
          },
          {
            type: "string",
            max: 512,
            message: "业务描述不能超过512个字！",
            trigger: "blur"
          }
        ]
      }
    };
  },
  mounted() {
  },
  methods: {
    handleSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          console.log('this.formValidate', this.formValidate)
          this.$emit("save", this.formValidate);
        } else {
          this.$Message.error("Fail!");
        }
      });
    },
    handleCancel(name) {
      this.$emit("cancel", false);
    }
  }
};
</script>
<style lang="scss" @scoped>
.add-risk-level {
  margin: 0 30px;
  label {
    font-size: 14px;
  }
  .form-content {
    .speical-span {
      width: 8.33333333%;
      float: left;
    }
    .ivu-date-picker {
      width: 100%;
    }
  }
}
</style>


