<template>
    <div class="history">
        <table class="tableBox" cellspacing="0" cellpadding="0">
            <thead>
                <tr>
                    <td v-for="(item,index) in tableHeader1">{{item.name}}</td>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item,index) in tableBody">
                    <td>{{index+1}}</td>
                    <td>{{item.name}}</td>
                    <td>{{item.busis}}</td>
                    <td>{{item.startTime}}</td>
                    <td class="show-detail" @click="shhowHistory">详情</td>
                </tr>
            </tbody>
        </table>
        <Modal :mask-closable="false"  
            v-model="showHistoryModal"
            :title="modelTitle"
            @on-cancel="cancel"
            class="dialog"
            :styles="{width: '40%'}"
            >
            <component :is="tabview" v-if="showHistoryModal" width="40%"></component>
            <div slot="footer"></div>
        </Modal>
    </div>
</template>
<script>
import historyDetail from './historyDetail'
export default {
    data(){
        return{
            showHistoryModal:false,
            modelTitle:'详情',
            tabview:'historyDetail',
            tableHeader1: [
                { name: "序" },
                { name: "部门" },
                { name: "更新时间" },
                { name: "更新人员" },
                { name: "更新内容" },
            ],
            tableBody: [{
                name: "A股",
                busis: "零售-运营管理部2019-12-21",
                personer: "zhansan",
                startTime: "2019-06-06",
                endTime: "2019-08-06",
                flag: false
            }],
        }
    },
    components:{
        historyDetail
    },
    methods:{
        shhowHistory(){
            this.showHistoryModal = true;
        },
        cancel(){

        }
    }
}
</script>
<style lang="scss">
    .history{
        .tab-title{
            font-size: 14px;
            color: #333;
            font-weight: bold;
        }
        table{
            width: 100%;
            font-size: 14px;
            color: #333333;
            margin-bottom: 20px;
            margin-top: 0;
            .show-detail{
                color: #0000CC;
                cursor: pointer;
                text-align: center;
            }
            &.first-tab{
                td{
                    border: .5px solid #ebecee;
                    height: 30px;
                    line-height: 30px;
                    padding-left: 20px;
                    &:nth-child(odd){
                        font-weight: bold;
                    }
                }
            }
            &.second-tab{
                thead{
                    tr{
                        td{
                            font-weight: bold;
                        }
                    }
                }
                tbody{
                    td{
                        &:nth-child(odd){
                            font-weight: normal;
                        } 
                    }
                }
            }
        }
    }
</style>


