<template>
  <div class="qutati-set">
    <div class="button-content1">
      <Button type="primary" @click="operate('1')">
        <i class="ivu-icon ivu-icon-md-add"></i>新增
      </Button>
    </div>
    <table class="tableBox" cellspacing="0" cellpadding="0">
      <thead>
        <tr>
          <td v-for="(item,index) in tableHeader1">{{item.name}}</td>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item,index) in tableBody">
          <td>{{index+1}}</td>
          <td>{{item.name}}</td>
          <td>{{item.busis}}</td>
          <td>{{item.startTime}}</td>
          <td>{{item.startTime}}</td>
        </tr>
      </tbody>
    </table>
    <Modal :mask-closable="false"  
      v-model="showModal"
      :title="modelTitle"
      @on-cancel="cancel"
      class="dialog"
      :styles="{width: '60%'}"
    >
      <component :is="tabview" width="60%" @cancel="cancelDialog" @save="saveDatas" :dataObj="dataObj" ></component>
      <div slot="footer"></div>
    </Modal>
  </div>
</template>
<script>
import addQutatiSet from "./addQutatiSet";

export default {
  data() {
    return {
      showModal: false,
      modelTitle: "新增定性因子",
      tabview: "addQutatiSet",
      dataObj:{
        type: "1", //新增
        data:[],
      },
      tableHeader1: [
        { name: "序" },
        { name: "模型代码" },
        { name: "规则因子" },
        { name: "因子公式" },
        { name: "备注" }
      ],
      tableBody: [
        {
          name: "A股",
          busis: "零售-运营管理部2019-12-21",
          personer: "zhansan",
          startTime: "2019-06-06",
          endTime: "2019-08-06"
        }
      ]
    };
  },
  components: {
    addQutatiSet
  },
  methods: {
    cancel() {},
    saveDatas(data) {
      this.showModal = false;
      this.tableBody[this.dataIndex] = data;
    },
    cancelDialog(val) {
      this.showModal = val;
    },
    operate(type) {
      if (type == "1") {
        this.tabview = "addQutatiSet";
        this.showModal = true;
      }
    }
  }
};
</script>
<style lang="scss">
.qutati-set {
  .tab-title {
    font-size: 14px;
    color: #333;
    font-weight: bold;
  }
  table {
    width: 100%;
    font-size: 14px;
    color: #333333;
    margin-bottom: 20px;
    margin-top: 0;
    .show-detail {
      color: #0000cc;
      cursor: pointer;
      text-align: center;
    }
    &.first-tab {
      td {
        border: 0.5px solid #ebecee;
        height: 30px;
        line-height: 30px;
        padding-left: 20px;
        &:nth-child(odd) {
          font-weight: bold;
        }
      }
    }
    &.second-tab {
      thead {
        tr {
          td {
            font-weight: bold;
          }
        }
      }
      tbody {
        td {
          &:nth-child(odd) {
            font-weight: normal;
          }
        }
      }
    }
  }
}
</style>


