<template>
  <div class="risk-level-set">
    <div class="topTitle">
      <span class="topTitle-span1">模型管理</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">{{typeNm}}洗钱风险模型管理</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">定性模型设置</span>
    </div>
    <div class="contentCellBox">
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>数据列表
          <div class="button-content">
            <Button type="primary" @click="operate('1')">
              <i class="ivu-icon ivu-icon-md-add"></i>新增
            </Button>
          </div>
        </div>
        <div class="contentCellBody">
          <Row>
            <table class="tableBox">
              <thead>
                <tr>
                  <td v-for="(item,index) in tableHeader">{{item.name}}</td>
                </tr>
              </thead>
              <tbody>
                <template v-for="(item,index) in tableBody">
                  <tr>
                    <td>{{index+1}}</td>
                    <td>
                      <span class="spanBox" @click="operate('2', index)">修改</span>
                      <span class="spanBox" @click="operate('3', index)">删除</span>
                      <span class="spanBox" @click="operate('4', index)">历史</span>
                      <span class="spanBox" @click="operate('5', index)">设置</span>
                    </td>
                    <td>{{item.code}}</td>
                    <td>{{item.name}}</td>
                    <td>
                        <i-switch v-model="item.flag"   @on-change="changeUseSts(item)" >
                            <span slot="open"></span>
                            <span slot="close"></span>
                        </i-switch>
                    </td>
                    <td>{{item.roleDescribe}}</td>
                    <td>{{item.roleDescribe}}</td>
                    <td>{{item.Remarks}}</td>
                    <td>{{item.Remarks}}</td>
                  </tr>
                </template>
              </tbody>
            </table>
          </Row>
          <div class="flexCenter pageBox">
            <Page :total="100" show-elevator show-sizer></Page>
          </div>
        </div>
      </div>
    </div>
    <Modal :mask-closable="false"  
      v-model="showModal"
      :title="modelTitle"
      @on-cancel="cancel"
      class="dialog"
      :styles="{width: '80%'}"
    >
      <component :is="tabview" @cancel="cancelDialog" @save="saveDatas" :dataObj="dataObj" width="80%"></component>
      <div slot="footer"></div>
    </Modal>
  </div>
</template>
<script>
import addQutatiLevel from "../common/qutatiModelSet/addQutatiLevel";
import history from "../common/qutatiModelSet/history";
import qutatiSet from "../common/qutatiModelSet/qutatiSet";
export default {
  components: {
    addQutatiLevel,
    history,
    qutatiSet
  },
  data() {
    return {
      tabview:'addQutatiLevel',
      showModal: false,
      dataObj:{
        type: "1", //1新增，2修改，3删除，4历史，5设置
        data:[],
      },
      modelTitle: "新增",
      type: this.$route.params.type,
      typeNm: "业务",
      tableHeader: [
        { name: "序" },
        { name: "操作" },
        { name: "规则名称" },
        { name: "规则模型" },
        { name: "状态" },
        { name: "需要调整的等级" },
        { name: "目标等级" },
        { name: "优先级" },
        { name: "备注" },
      ],

      tableBody: [
        {
          id: "12",
          code: "111",
          name: "低",
          roleDescribe: "0",
          Remarks: "50"
        }
      ]
    };
  },
  mounted() {},
  methods:{
    cancel() {},
    operate(type, index){
      switch (type) {
        case '1':
          this.modelTitle = '新增'
          this.tabview = 'addQutatiLevel';
          this.showModal = true;
          this.dataObj = {
            type:1,
            data:[]
          };
          break;
        case '2':
          this.modelTitle = '修改'
          this.dataObj = {
            type:2,
            data:[]
          };
          this.tabview = 'addQutatiLevel';
          this.showModal = true;
          break;
        case '3':
          this.$Modal.confirm({
              title:'提示',
              content: "是否删除？",
              okText: "确认",
              cancelText: "取消",
              onOk: () => {
              },
              onCancel: () => {
              }
          });
          break;
        case '4':
          this.modelTitle = '历史'
          this.tabview = 'history';
          this.showModal = true;
          break;
        case '5':
          this.modelTitle = '指标条件定性设置';
          this.tabview = 'qutatiSet';
          this.showModal = true;
          break;
      }
    },
    saveDatas(data){
      this.showModal = false;
      this.tableBody[this.dataIndex] = data;
    },
    cancelDialog(val) {
      this.showModal = val;
    },
  },
  watch: {
    '$route':{
      handler(to, form){
        this.$route.params.type == 1
        ? (this.typeNm = "业务")
        : (this.typeNm = "产品");
      },
      immediate:true
    },
  }
};
</script>
<style lang="scss" scoped>
.risk-level-set {
  .button-content {
    float: right;
  }
}
</style>


